import React from 'react';

interface Props {
    bpi: object;
}

class Prices extends React.Component<Props, any> {
    state = {
        currency: 'USD'
    }

    render(): React.ReactNode {
        return (<div>
            <ul className="list-group">
                {this.props.bpi.bpi.USD.description}
            </ul>
        </div>);
    }
}

export default Prices;
